.CardActive .Button {
	background: var(--color-bg-link);
}
.CardInactive .Button {
	background: var(--color-bg-brand);
}

.CardActive .CheckBox {
	background: var(--color-bg-link);
}
.CardInactive .CheckBox {
	background: var(--color-bg-brand);
}

.CardActive .GridButtons {
	align-items: center;
	justify-content: center;
	flex-direction: column;
	display: flex;
}
.CardInactive .GridButtons {
	align-items: center;
	justify-content: center;
	flex-direction: column;
	display: flex;
}

.CardActive {
	background: var(--color-bg-link);
}
.CardInactive {
	background: var(--color-bg-brand);
}
